import { getTemplate } from './getTemplate.utils';

export function getListItemsHtml<T>(items, json:any, getElement:any): T[] {
  const listItems = [];

  for (let i = 0; i < (items.data !== undefined ? items.data.length : items.length); i++) {
    listItems.push(getTemplate(getElement(items.data !== undefined ? items.data[i] : items[i]), json));
  }

  return listItems;
}