import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewEncapsulation,
} from '@angular/core';
import { BehaviorSubject, Subject, takeUntil } from 'rxjs';
import { ProductionOrderDto } from '../../types/productionOrderDto';
import { CdProductionStepDto } from '../../types/productionStepDto';
import { CommonModule } from '@angular/common';
import { MaterialModule } from '../../../material/material.module';
import { MatTreeFlatDataSource, MatTreeFlattener } from '@angular/material/tree';
import { ProductionOrderNode } from '../../types/productionOrderNode';
import {PipeModule} from '../../../shared/pipes/pipe.module';
import { FlatTreeControl } from '@angular/cdk/tree';
import { getListItemsHtml } from '../../../shared/utils/getListItemsHtml.utils';

export class FlatNode {
  expandable: boolean;
  level: number;
  id: number;
}

const transformer = (node: ProductionOrderNode, level: number) => {
  return {
    expandable: !!node.children && node.children.length > 0,
    ...node,
    level: level,
    id: node.id,
  };
};

@Component({
  selector: 'app-list-item',
  templateUrl: './list-item.component.html',
  standalone: true,
  styleUrls: ['./list-item.component.scss'],
  encapsulation: ViewEncapsulation.None,
  imports: [CommonModule, MaterialModule, PipeModule],
})

export class ListItemComponent implements OnInit, OnDestroy {
  treeControl = new FlatTreeControl<FlatNode>(
    node => node.level,
    node => node.expandable,
  );
  treeFlattener = new MatTreeFlattener(
    transformer,
    node => node.level,
    node => node.expandable,
    node => node.children,
  );
  @Input() data: BehaviorSubject<ProductionOrderDto[] | CdProductionStepDto[] | MatTreeFlatDataSource<ProductionOrderNode, FlatNode>> = 
  new BehaviorSubject<ProductionOrderDto[] | CdProductionStepDto[] | MatTreeFlatDataSource<ProductionOrderNode, FlatNode>>([] || new MatTreeFlatDataSource(this.treeControl, this.treeFlattener));  
  @Input() template;
  @Input() format;
  @Input() index = 0;
  @Input() isDraggable?:boolean = false;
  @Input() isDisabled?:boolean = false;
  @Input() isClass?: boolean = false;
  @Output() clickListItem? = new EventEmitter();
  @Output() mouseDownListItem? = new EventEmitter();
  @Output() mouseUpListItem? = new EventEmitter();
  @Output() dragStartListItem? = new EventEmitter();
  @Output() dragEndListItem? = new EventEmitter();

  listHtml:string[] = [];

  private _unsubscribeAll = new Subject<any>();

  ngOnInit(): void {
    this.data.pipe(takeUntil(this._unsubscribeAll)).subscribe(value => {
      this.listHtml = getListItemsHtml(value, this.template, this.format);
    });
  }

  ngOnDestroy(): void {
    this._unsubscribeAll.next({});
    this._unsubscribeAll.complete();
  }
}
